.brands {
	width: 100%;
	max-width: 100%;
	background-color: rgba(15, 15, 15, 1);
}

.brands__wrap {
	margin: 0 auto;
	max-width: 1200px;
	padding-top: 60px;
	padding-bottom: 75px;
}

.brands__title {
	font-weight: 700;
    font-size: 36px;
    line-height: 48px;
    text-transform: uppercase;
    font-family: 'Playfair Display', "Arial", "sans-serif";
    text-align: center;
    position: relative;
    padding-bottom: 40px;
    margin-bottom: 40px;
}

.brands__title::after {
	content: "";
	position: absolute;
	width: 120px;
	height: 1px;
	background-color: rgba(255, 255, 255, 1);
	margin: 0 auto;
	left: 0;
	right: 0;
	bottom: 0;
}

.brands__list {
	display: flex;
	padding: 0 30px;
	flex-wrap: wrap;
}

.brands__item {
	flex-grow: 1;
	display: flex;
	justify-content: center;
}

.brands__img {
	width: 255px;
	height: 94px;
}