* {
	margin: 0;
	padding: 0;
}

.page {
	height: 100%;
}

.page-body {
	font-family: "PT Sans", "Arial", "sans-serif";
	font-weight: 400;
	color: rgba(255, 255, 255, 1);
	background-color: rgba(0, 0, 0, 1);
	margin: 0;
	padding: 0;
	overflow-x: hidden;
	min-height: 100%;
}

a {
	text-decoration: none;
}

ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

img {
	max-width: 100%;
	height: auto;
}

.hidden {
	position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}
