

.page-header {
	width: 100%;
	max-width: 100%;
	min-height: 124px;
}

.page-header__wrapper {
	margin: 0 auto;
}

.info-head__burger {
	display: none;
}

.info-head__img {
	width: 102px;
	height: 26px;
}

.info-user__link,
.info-user__text,
.info-user__time,
.form-user__login,
.form-user__logup {
	color: rgba(150, 150, 150, 1);
	font-size: 12px;
	line-height: 16px;
}

.nav__link {
	color: rgba(150, 150, 150, 1);
	font-size: 14px;
    line-height: 18px;
    text-transform: uppercase;
}

.info-user {
	min-height: 44px;
	display: flex;
	align-items: center;
}

.info-user__wrap {
	max-width: 1200px;
	margin: 0 auto;
	width: 100%;
}
.info-user__position {
	padding: 0 45px;
	display: flex;
	justify-content: space-between;
}

.info-user__link {
	position: relative;
	padding-left: 30px;
	padding-right: 15px;
	margin-right: 15px;
}

.info-user__link::before {
	content: "";
	position: absolute;
	background: url("../img/tel.svg") no-repeat center;
	width: 14px;
	height: 14px;
	left: 0;
}

.info-user__link::after {
	content: "";
	position: absolute;
	width: 1px;
	height: 10px;
	background-color: rgba(150, 150, 150, 1);
	right: 0;
	top: 2px;
}

.info-user__text {
	padding-right: 14px;
	position: relative;
	margin-right: 15px;
}

.info-user__text::after {
	content: "";
	position: absolute;
	width: 1px;
	height: 10px;
	background-color: rgba(150, 150, 150, 1);
	right: 0;
	top: 2px;
}

.form-user__login {
	position: relative;
	padding-left: 25px;
	padding-right: 6px;
}

.form-user__login::before {
	content: "";
	position: absolute;
	background: url("../img/log-in.svg") no-repeat center;
	width: 14px;
	height: 14px;
	left: 0;
	top: 1px;
}

.form-user__login::after {
	content: "";
	position: absolute;
	width: 1px;
	height: 10px;
	background-color: rgba(150, 150, 150, 1);
	transform: rotate(30deg);
	right: 0;
	top: 3px;
}

.info-user__work {
	display: flex;
}

.info-head {
	min-height: 80px;
	background-color: rgba(15, 15, 15, 1);
	
}

.info-head__wrap {
	max-width: 1200px;
	margin: 0 auto;
}

.info-head__position {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 0 45px;
}

.nav__list {
	display: flex;
}

.nav__item {
	padding: 0 20px;
	height: 80px;
	display: flex;
	align-items: center;
}

.nav__item--active {
	background-color: rgba(31, 31, 31, 1);
	color: rgba(255, 255, 255, 1);
}

.nav__link {
	display: block;
}

.link-basket {
	background: url("../img/basket.svg") no-repeat center;
	width: 22px;
	height: 22px;
}

.link-search {
	background: url("../img/search.svg") no-repeat center;
	width: 22px;
	height: 22px;
}


@media (max-width: 767px) {
	.nav {
		display: none;
	}

	.info-head__burger {
		display: block;
		position: relative;
		height: 80px;
		width: 50px;
	}

	.info-head__burger::before,
	.info-head__burger::after {
		content: "";
		position: absolute;
		width: 100%;
		height: 2px;
		background-color: rgba(150, 150, 150, 1);
	}

	.info-head__burger::before {
		top: 23px;
	}

	.info-head__burger::after {
		bottom: 23px;
	}

	.burger-element {
		position: absolute;
		width: 100%;
		height: 2px;
		background-color: rgba(150, 150, 150, 1);
		margin: auto;
		top: 0;
		bottom: 0;
	}

	.info-user__position {
		padding: 0 20px;
		display: flex;
		flex-direction: column;
	}

	.info-head__position {
		padding: 0 20px;
	}

	.info-user__wrap {
		max-width: initial;
	}

	.form-user {
		margin: 0 auto;
	}

	.info-user__work {
		margin: 0 auto;
		display: flex;
		align-items: center;
		
	}

	.info-user__link {
	}

	.info-user__time {
		margin-right: 15px;
	}
}