.page-footer {
	width: 100%;
	max-width: 100%;
	min-height: 50px;
	background-color: rgba(15, 15, 15, 1);
	display: flex;
	align-items: center;
}

.page-footer__wrapper {
	max-width: 1200px;
	margin: 0 auto;
	width: 100%;
}

.page-footer__position {
	display: flex;
	justify-content: space-between;
	padding: 0 45px;
}

.page-footer__text {
	font-size: 12px;
    line-height: 16px;
    color: rgba(208, 208, 208, 1)
}

.page-footer__link {
	font-weight: 700;
    font-size: 12px;
    line-height: 16px;
    color: rgba(208, 208, 208, 1);
    text-decoration: underline;
}