.review {
	width: 100%;
	max-width: 100%;
}

.review__wrap {
	max-width: 1920px;
	margin: 0 auto;
	display: flex;
}

.review__content {
	width: 50%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	flex-grow: 1;
}

.review__subtitle {
	font-size: 36px;
    line-height: 48px;
    text-transform: uppercase;
    font-family: 'Playfair Display', "Arial", "sans-serif";
    padding-bottom: 25px;
    position: relative;
    margin-bottom: 47px;
    font-weight: 400;
}

.review__subtitle::after {
	content: "";
	position: absolute;
	width: 120px;
	height: 1px;
	background-color: rgba(255, 255, 255, 1);
	margin: 0 auto;
	left: 0;
	right: 0;
	bottom: 0;
}

.review__text {
	font-size: 14px;
	line-height: 21px;
	width: 45%;
	text-align: center;
	margin-bottom: 60px;
}

.review__link {
	font-size: 18px;
    line-height: 23px;
    text-align: center;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: inherit;
    padding: 12px 28px;
    border: 1px solid rgba(255, 255, 255, 1);
}

.review__img {
	width: 880px;
	height: 600px;
	object-fit: cover;
}

.review__picture {
	flex-grow: 1;
}

@media (max-width: 1225px) {
	.review {
		background: url("../img/smoking.jpg") no-repeat center;
		min-height: 600px;
		display: flex;
		align-items: flex-end;
	}

	.review__wrap {
		flex-wrap: wrap;
		padding-bottom: 30px;
	}

	.review__picture {
		display: none;
	}
}

@media (max-width: 767px) {
	.review__text {
		width: 80%;
	}
}