.stock {
	width: 100%;
	max-width: 100%;
	min-height: 400px;
	background: grey url("../img/stock-phone.jpg") no-repeat center;
	background-size: cover;
}

.stock__wrap {
	max-width: 1200px;
	margin: 0 auto;
	padding-top: 80px;
}

.stock__position {
	display: flex;
	padding: 0 15px 0 45px;
}

.stock__subtitle {
	font-size: 24px;
    line-height: 32px;
    text-transform: uppercase;
    font-family: 'Playfair Display', "Arial", "sans-serif";
    margin-bottom: 40px;
}

.stock__text,
.stock__par {
	font-size: 14px;
	line-height: 21px;
}

.stock__par {
	margin-bottom: 34px;
}

.stock__text {
	width: 94%;
}

.stock__link {
	font-size: 14px;
	line-height: 21px;
	color: inherit;
	display: block;
	background-color: rgba(15, 15, 15, 1);
	padding: 10px 20px;
}

.stock__item {
	width: max-content;
}

.stock__field {
	width: 210px;
	height: 42px;
	opacity: 0.4;
	padding-left: 20px;
}

.stock__button {
	font-size: 14px;
    line-height: 21px;
    letter-spacing: 0.05em;
    cursor: pointer;
    padding: 10px 20px;
    background-color: rgba(15, 15, 15, 1);
    color: inherit;
}

.stock__store,
.stock__categories,
.stock__submit {
	width: 33%;
	flex-grow: 1;
}

.stock__list {
	display: flex;
	flex-wrap: wrap;
	gap: 20px;
	padding-left: 5px;
}

.stock__submit-content {
	padding-left: 7px;
}

.stock__mail {
	display: flex;
}

@media (max-width: 1225px) {
	.stock__mail {
		flex-wrap: wrap;
		width: min-content;
	}

	.stock__button {
		flex-grow: 1;
	}
}

@media (max-width: 767px) {
    .stock__wrap {
    	padding-top: 30px;
    	padding-bottom: 30px;
    }

	.stock__position {
		padding: 0 15px 0 15px;
		flex-wrap: wrap;
	}

	.stock__store,
    .stock__categories,
    .stock__submit {
	width: 100%;
    }

    .stock__store,
    .stock__categories {
    	margin-bottom: 30px;
    }

    .stock__subtitle {
    	text-align: center;
    	margin-bottom: 20px;
    }

    .stock__text {
    	margin: 0 auto;
    	text-align: center;
    }

    .stock__list {

    }

    .stock__item {
    	margin: 0 auto;
    }

    .stock__par {
    	text-align: center;
    	margin-bottom: 10px;
    }

    .stock__mail {
    	margin: 0 auto;
    }
}