.show {
	width: 100%;
	max-width: 100%;
	background: grey url("../img/show-phone.jpg") no-repeat center;
	background-size: cover;
	min-height: 810px;
	display: flex;
	align-items: center;
	justify-content: center;
}

.show__wrap {
	max-width: 1200px;
	margin: 0 auto;
}

.show__position {
	padding: 0 20px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.show__content {
	width: 320px;
	height: 320px;
	box-sizing: border-box;
	border: 4px solid #FFFFFF;
	text-transform: uppercase;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	margin-bottom: 82px;
}

.show__par {
	font-size: 22px;
    line-height: 28px;
}

.show__text {
	font-size: 16px;;
	line-height: 24px;
	text-align: center;
	width: 63%;
	text-transform: uppercase;
}

.show__logo {
	margin-bottom: 5px;
	width: 185px;
	height: 86px;
}

@media (max-width: 767px) {
	.show__content {
		width: 250px;
		height: 250px;
		border-radius: 5px;
		overflow: hidden;
	}
}