.collection {
	width: 100%;
	max-width: 100%;
}

.collection__wrap {
	max-width: 1920px;
	margin: 0 auto;
	display: flex;
}

.collection__subtitle {
	font-weight: 700;
    font-size: 36px;
    line-height: 48px;
    font-family: 'Playfair Display', "Arial", "sans-serif";
    text-transform: uppercase;
    text-align: center;
    position: relative;
    padding-bottom: 42px;
    margin-bottom: 50px;
}

.collection__catalog .collection__subtitle {
	padding-bottom: 20px;
	margin-bottom: 30px;
}

.collection__subtitle::after {
	content: "";
	position: absolute;
	width: 120px;
	height: 1px;
	background-color: rgba(255, 255, 255, 1);
	left: 0;
	right: 0;
	bottom:  0;
	margin: 0 auto;
}

.collection__desc {
	font-size: 18px;
    line-height: 24px;
    font-family: 'Playfair Display', "Arial", "sans-serif";
    margin-bottom: 11px;
}

.collection__cost {
	color: rgba(208, 208, 208, 1);
	font-weight: 700;
    font-size: 16px;
    line-height: 21px;
    font-family: 'Playfair Display', "Arial", "sans-serif";
}

.collection__wrap-link {
	border: 1px solid #FFFFFF;
    text-align: center;
    width: 138px;
    padding: 12px 0;
}

.collection__link {
	font-size: 18px;
    line-height: 23px;
    color: rgba(255, 255, 255, 1);
    text-transform: uppercase;
    display: block;
    width: 138px;
    letter-spacing: 0.1em;
}

.collection__content {
	flex-grow: 1;
	padding-top: 92px;
}

.collection__catalog {
	background: grey url("../img/collection-photo.jpg") no-repeat center;
	background-size: cover;
	width: 879px;
	height: 730px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-end;
}

.collection__position {
	padding-top: 40px;
	display: flex;
	flex-direction: column;
	align-items: center;
	padding-bottom: 97px;
}

.collection__list {
	display: flex;
	justify-content: space-evenly;
	flex-wrap: wrap;
	padding: 0 30px;
}

.collection__picture {
	margin-bottom: 21px;
	width: 260px;
	height: 325px;
	object-fit: cover;
}

.collection__item {
	margin: 0 10px 20px;
}

@media (max-width: 1225px) {
	.collection__wrap {
		flex-wrap: wrap;
	}

	.collection__catalog {
		margin: 0 auto;
	}

	.collection__position {
		padding: 40px 20px 97px;
	}

}