.new {
	width: 100%;
	max-width: 100%
}

.new__wrap {
	max-width: 1200px;
	margin: 0 auto;
	padding-top: 117px;
	padding-bottom: 110px;
	display: flex;
	flex-direction: column;
}

.new__title {
	text-align: center;
	font-size: 36px;
    line-height: 48px;
    font-family: 'Playfair Display', "Arial", "sans-serif";
    text-transform: uppercase;
    position: relative;
    padding-bottom: 39px;
    margin-bottom: 81px;
}

.new__title::after {
	content: "";
	position: absolute;
	width: 120px;
	height: 1px;
	background-color: rgba(255, 255, 255, 1);
	margin: 0 auto;
	left: 0;
	right: 0;
	bottom: 0;
}

.new__list {
	display: flex;
	flex-wrap: wrap;
	padding: 0 33px;
}

.new__position {
	width: max-content;
	display: flex;
	flex-direction: column;
}

.new__item {
	display: flex;
	justify-content: center;
	margin: 0 10px 40px;
	flex-grow: 1;
}

.new__desc {
	font-size: 18px;
    line-height: 24px;
    font-family: 'Playfair Display', "Arial", "sans-serif";
    margin-bottom: 10px;
}

.new__cost {
	font-weight: 700;
    font-size: 16px;
    line-height: 21px;
    font-family: 'Playfair Display', "Arial", "sans-serif";
    color: rgba(208, 208, 208, 1);
}

.new__img {
	margin-bottom: 25px;
	width: 260px;
	height: 325px;
}